import React from "react";
import { Link } from "react-router-dom";



import "./header.css";

const Header = () => {
  return (
    <div className="o-header1">
      <h5 className="o-learn">LearnPro</h5>
      <div className="o-access-container">
        
          <Link to="/Actividad" >
            <button className="o-button o-button-signin">Inicio</button>
          </Link>
          
          <Link to="/barcoPapel" >
            <button className="o-button o-button-signin">Actividades</button>
          </Link>
          
        
        
         
        
      </div>
    </div>
  );
};

export default Header;
