import React, { Component } from 'react'
import Header from '../Header/header'
import barco from '../../../src/Img/barcofull.svg'
import R1 from '../../Img/Rompecabezas/R1.png'
import R2 from '../../Img/Rompecabezas/R2.png'
import R3 from '../../Img/Rompecabezas/R3.png'
import R4 from '../../Img/Rompecabezas/R4.png'
import R5 from '../../Img/Rompecabezas/R5.png'
import R6 from '../../Img/Rompecabezas/R6.png'

import fig1 from '../../Img/Rompecabezas/fig1.jpg'
import figura from '../../../src/Img/figura.svg'

import $ from 'jquery';
import 'jquery-ui-dist/jquery-ui';
import { Link } from "react-router-dom";

import './Rompecabezas.css'

class Rompecabezas extends Component {
    constructor() {
        super();
        this.state = {
            correctCards: 6
        }
    }

    /*---------------------------------función que inicializa-------------------------------*/
  


    init = () => {
        const me = this;
       var correct = 0;
        // Hide the success message
        $('#successMessage').hide();
        $('#successMessage').css({
            left: '580px',
            top: '250px',
            width: 0,
            height: 0
        });

        // Reset the game
        this.state.correctCards = 0;
        $('#cardPile').html('');
        $('#cardSlots').html('');

        // Create the pile of shuffled cards
        var numbers = [1, 2, 3, 4, 5, 6,];
        var terms = ['1', '2', '3', '4', '5', '6'];
        // Arreglo de imagenes
        var arrayCuts = [R1, R2, R3, R4, R5, R6];

        for (var i = 0; i < 6; i++) {
            $('<div>' + '<img class="o-img" src="' + arrayCuts[i] + '"/>' + terms[i] + '</div>').data('number', numbers[i]).attr('id', 'card' + numbers[i]).appendTo('#cardPile').draggable({
                stack: '#cardPile div',
                cursor: 'move',
                revert: true
            });
        }

        // Create the card slots
        var words = ['Paso 1', 'Paso 2', 'Paso 3', 'Paso 4', 'paso 5', 'paso 6'];
        for (var i = 1; i <= words.length; i++) {
            $('<div>' + '<p class="o-steps">' + words[i - 1] + '</p>' + '</div>').data('number', i).appendTo('#cardSlots').droppable({
                accept: '#cardPile div',
                hoverClass: 'hovered',
                drop: function handleCardDrop(event, ui) {
                    var slotNumber = $(this).data('number');
                    var cardNumber = ui.draggable.data('number');

                    // If the card was dropped to the correct slot,
                    // change the card colour, position it directly
                    // on top of the slot, and prevent it being dragged
                    // again

                    if (slotNumber == cardNumber) {
                        ui.draggable.addClass('correct');
                        ui.draggable.draggable('disable');
                        $(this).droppable('disable');
                        ui.draggable.position({ of: $(this), my: 'center center', at: 'center center' });
                        ui.draggable.draggable('option', 'revert', false);

                        correct++;
                    
                    }

                    // If all the cards have been placed correctly then display a message
                    // and reset the cards for another go

                    if ( correct ==  6 ) {
                      $('#successMessage').show();
                      $('#successMessage').animate( {
                        left: '380px',
                        top: '200px',
                        width: '400px',
                        height: '100px',
                        opacity: 1
                      } );
                    }

                }
            });
        }

    }

    /*--------------------------------Primer paso de la actividad-----------------------------------*/
    Step = () => {
        return (
            <div className="o-content" >
                <div className="o-container-player">
                    <div id="cardPile"> </div>
                </div>
                <div className="o-container-answers">
                    <div id="cardSlots"> </div>
                </div>
                <div id="successMessage">
                    <h2>Congratulations!</h2>
                    <button onClick={this.init}>Play Again</button>
                </div>
            </div>

        );
    }

    begin = () => {
        //This is a arrow function
    }
    render() {
        return (
            <div>

                <div className="o-img-fondo">
                    <Header/>
                    
                    <div className="o-inicio">

                        <div className="o-column">

                            <div className="o-text">
                            <img alt="" src={figura} className="o-figura"/>
                            <h3 className="o-title-inicio"> 1</h3>
                            <p className="o-text-info"> ¡Ahora es tiempo de construir un rompecabezas,
                              recuerda que un rombpecabezas se compone de varias piezas que deben ser unidas en orden.</p>
                            </div>

                            <div className="o-text">
                            <img alt="" src={figura} className="o-figura"/>
                            <h3 className="o-title-inicio"> 2</h3>
                            <p className="o-text-info">Es importante identificar cuáles piezas van primero que otras, para asi poder ver la imagen completa,
                             tener en cuenta la imagen de referencia.</p>
                            </div>

                            <div className="o-text">
                            <img alt="" src={figura} className="o-figura"/>
                            <h3 className="o-title-inicio-3"> 3</h3>
                            <p className="o-text-info-3"> <span className="o-bold">¡Arrastra y Suelta! </span> </p>
                            </div>

                        </div>
                        
                       <div className="o-div-barco">
                    <img alt="" src={fig1} className="o-barco"/>
                    </div>
                        
                    </div>

                </div>
            <this.Step />
            <div className="o-back">
            <Link to="/Actividad" >
                <button className="o-button-inicio">Atrás</button>
                </Link>
            </div>

            </div>
        );
    }

    /**-----------------------Una vez el componente es renderizado -------------------------- */
    componentDidMount() {
        this.init();
    }

}
export default Rompecabezas;