import React, { Component } from 'react';

import Actividad from "../Actividad/Actividad/Actividad";
import barcoPapel from "../barcoPapel/barcoPapel"
import Rompecabezas from "../rompecabezas/Rompecabezas"
import manosLimpias from "../manosLimpias/manosLimpias"


import { BrowserRouter, Switch, Route } from "react-router-dom";

import './Main.css'

class Main extends Component {

    constructor(props) {
        super(props)
        this.state = {
            user: null
        }
        this.outputEvent = this.outputEvent.bind(this);
    }

    outputEvent = (param) => {
        this.setState({user: param})
    }

    render() {
        return (
            <BrowserRouter>
                <div className="App">
                    <Switch>
                        <Route path="/" exact component={Actividad} />
                        <Route path="/Actividad" exact component={Actividad} />
                        <Route path="/manosLimpias" component={manosLimpias} />
                        <Route path="/barcoPapel" component={barcoPapel} />
                       
                      
                        
                    </Switch>
                </div>
            </BrowserRouter>
        )
    }

}

export default Main